import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";

// 景区客流统计
export function queryCheckDutySatatic (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_checkwork/GetCheckDutySatatic',
    method: 'post',
    data: params
  })
}

// 游客画像
export function queryTouristsPortrait (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/BigData/GetTouristsPortrait',
    method: 'post',
    data: params
  })
}

