// 大数据
export function queryPeopleFlow (params) {
  return myrequest({
    url: '/people/wisegateway/interfaceAuth/selectUserSelfData',
    method: 'post',
    data: qs.stringify(params),
    headers: { 'content-type': 'application/x-www-form-urlencoded' }
  })
}


// 累计入园人数
export function queryTotalPeople (pageNum, pageSize, proId) {
  return myrequest({
    url: '/totalPeople/api/lbs/getTotalPeople',
    method: 'get',
    params: { pageNum, pageSize, proId }
  })
}

// 获取今日
export function queryToDayPeople () {
  return myrequest({
    url: '/totalPeople/api/ticket/ticketToDayPeople',
    method: 'get',
    params: {}
  })
}


