import {
  queryAccessTokenByPort,
  detailByUsername,
  getRoleList,
  getDepartmentList,
} from "@/api/userApi";

// 获取新accesstoken
export function getAccessTokenByPort (callback) {
  let info = {
    source: "APP"
  }
  queryAccessTokenByPort(info).then(response => {
    localStorage.setItem("access_token", response.data)
  })
}


// 生成签名
export function getSignature (app_id, app_key, rand) {
  let arr = { app_id, app_key, rand };
  let res = Object.values(arr).sort();
  let str = res.join("");
  const sha1 = require('js-sha1');
  return sha1(str);
}


// 获取用户信息
export function getUserInformation (username) {
  detailByUsername({ username: username }).then(response => {//根据用户名获取用户账号信息
    let userInfo = response.data//用户名获取用户账号信息列表
    localStorage.setItem("userInfo", JSON.stringify(userInfo)) // 保存用户信息
    // 获取用户角色
    getRole()
    // 用户登录后使用uid登录语音
    uidLogin()
  })
}

export function uidLogin () {
  // 用户登录后使用uid登录语音
  try {
    if (window.android) {
      window.android.uidLogin(JSON.parse(localStorage.getItem("userInfo")).uid)
    } else if (window.webkit) {
      window.webkit.messageHandlers.uidLogin.postMessage(JSON.parse(localStorage.getItem("userInfo")).uid)
    }
  } catch (err) {
    console.error(err);
  }

}

// 获取用户角色
export async function getRole () {
  let userInfo = JSON.parse(localStorage.getItem("userInfo"))
  let info = {
    uid: userInfo.uid
  }
  await getRoleList(info).then(response => {//根据用户名获取用户角色
    let roleList = response.data.list
    let roleInfoList = [] // 用户角色列表
    roleList.forEach(item => {
      roleInfoList.push(item.role_key)
    });
    localStorage.setItem("roleInfoList", JSON.stringify(roleInfoList)) // 保存用户信息
  })

}

//获取用户所在部门
export function getDepartment (callback) {
  let userInfo = JSON.parse(localStorage.getItem("userInfo"))
  let uid = {
    uid: userInfo.uid
  }
  getDepartmentList(uid).then(response => {
    let partList = response.data.list[0]
    if (typeof callback == "function") {
      // 回调函数传参
      callback(partList);
    }
  })
}
