<!-- 布局tabbar页面 -->
<template>
  <van-row style="height: 100%">
    <router-view />
    <van-tabbar v-model="bottomNav"
                @change="changeNav"
                :safe-area-inset-bottom="true">
      <van-tabbar-item replace
                       to="/home"
                       icon="home-o">工作台</van-tabbar-item>
      <van-tabbar-item replace
                       to="/message/list"
                       icon="chat-o">消息</van-tabbar-item>
      <van-tabbar-item replace
                       to="/addressBook"
                       icon="friends-o">通讯录</van-tabbar-item>
      <van-tabbar-item replace
                       to="/mine/personalCenter"
                       icon="user-circle-o">我的</van-tabbar-item>
    </van-tabbar>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { mapState } from 'vuex'
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
    };
  },
  //监听属性 类似于data概念
  computed: {
    ...mapState(["bottomNav"])
  },
  //监控data中的数据变化
  watch: {
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    changeNav (index) {
      this.$store.commit("bottomNav", index)
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
.van-tabbar-item {
  // padding-bottom: 12px;
}
.van-tabbar {
  height: 50px;
}
</style>