import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getters'


Vue.use(Vuex)

// https://webpack.js.org/guides/dependency-management/#requirecontext
const modulesFiles = require.context('./modules', true, /\.js$/)

// you do not need `import app from './modules/app'`
// it will auto require all vuex module from modules file
const modules = modulesFiles.keys().reduce((modules, modulePath) => {
  // set './app.js' => 'app'
  const moduleName = modulePath.replace(/^\.\/(.*)\.\w+$/, '$1')
  const value = modulesFiles(modulePath)
  modules[moduleName] = value.default
  return modules
}, {})

const store = new Vuex.Store({
  state: {  // 自定义的共享状态
    homeScroll: 0,   // 定义一个isTabbarShow状态
    bottomNav: 0,    // 底部导航的下标
    /**工单处理 */
    upImgList: [],    // 工单处理展示的上传的图片列表
    pictureList: [],  // 上传后的图片列表
    CahrgeContent: '', // 工单处理时的反馈信息
    //tab
    active: 0,
    userInfo: {}
  },
  mutations: {  // 在这下面修改状态
    homeScroll (state, data) { // 这里state就代表this.$store
      state.homeScroll = data
    },
    bottomNav (state, data) {
      state.bottomNav = data
    },
    upImgList (state, data) {
      state.upImgList = data
    },
    pictureList (state, data) {
      state.pictureList = data
    },
    CahrgeContent (state, data) {
      state.CahrgeContent = data
    },
    active (state, data) {
      state.active = data
    },
    userInfo (state, data) {
      state.userInfo = data
    }
  },
  modules,
  getters
})


export default store