import myrequest from '@/utils/axios'

const token = localStorage.getItem("access_token")

import { jiekouOnline, usrvOnline } from "./onLineBaseUrl";




// 获取access_token, 新接口
export function queryAccessTokenByPort (params) {
  return myrequest({
    url: jiekouOnline + '/jiekou/common-api/common/login/getAccessToken',
    method: 'post',
    data: params
  })
}

// 登录账户密码登录
export function authByAccount (params) {
  return myrequest({
    url: usrvOnline + '/usrv/user/admin/auth-by-account?access_token=' + token,
    method: 'post',
    data: params
  })
}

// 人脸识别-人脸登录 （从特定人员库中查询用户是否存在）
export function faceLogin (params) {
  return myrequest({
    url: usrvOnline + '/usrv/uc/iai/iai-search-persons?access_token=' + token,
    method: 'post',
    data: params
  })
}



