<template>
  <div class='dt-home'>
    <div class="top">
      <div class="top-row1">
        <div @click="navGoTo('PersonalCenter')"
             class="top-user">
          <div class="top-row1-div">
            <el-image :src="require('@/assets/img/home/avatar.svg')"></el-image>
          </div>
          <div class="top-row1-div">
            {{real_name}}
          </div>
        </div>
        <div @click="navGoTo('SignIn')"
             class="top-signIn">
          <div class="top-row1-div">
            <el-image :src="require('@/assets/img/home/clock.svg')"></el-image>
          </div>
          <div class="top-row1-div">
            打卡签到
          </div>
        </div>
      </div>
      <div class="top-row2">
        <div class="top-tasks"
             @click="navGoTo('MOOrderHead')">
          <div class="top-row2-div">
            <van-badge :content="orderMessage"
                       max="99">
              <el-image :src="require('@/assets/img/home/我的工单.svg')"></el-image>
            </van-badge>
          </div>
          <div class="top-row2-div">
            我的工单
          </div>
        </div>
        <div class="top-tasks"
             @click="navGoTo('MTTask')">
          <div class="top-row2-div">
            <van-badge :content="taskMessage"
                       max="99">
              <el-image :src="require('@/assets/img/home/task.svg')"></el-image>
            </van-badge>
          </div>
          <div class="top-row2-div">
            我的任务
          </div>
        </div>
        <div class="top-tasks"
             @click="navGoTo('OrderNewTicket')">
          <div class="top-row2-div">
            <el-image :src="require('@/assets/img/home/reback.svg')"></el-image>
          </div>
          <div class="top-row2-div">
            工单上报
          </div>
        </div>
      </div>
    </div>
    <div class="content-container">
      <div class="content">
        <div class="content-row1">
          <div class="onNum">
            <div>今日检票数</div>
            <div class="num">{{intoNum}}</div>
          </div>
          <div class="inNum">
            <span>今日售票数</span>
            <span class="num">{{ticketNum}}</span>
            <span class="content-row1-partition"></span>
            <span>昨日检票数</span>
            <span class="num">{{yesterdayInto}}</span>
          </div>
          <van-image class="content-row1-img"
                     :src="require('@/assets/img/home/robot.png')"></van-image>
        </div>
        <div class="content-row2">
          <div class="content-row2-title">我的应急</div>
          <div class="content-row2-emergencys">
            <div class="content-row2-box">
              <a :href="'http://58.220.201.130:9999/scooper-zlw-app/#/event?uid='+uid">
                <van-badge :content="eventCount"
                           max="99">
                  <el-image :src="require('@/assets/img/home/应急事件.svg')"></el-image>
                </van-badge>
                <div class="content-row2-text">
                  应急事件
                </div>
              </a>
            </div>
            <div class="content-row2-box">
              <a :href="'http://58.220.201.130:9999/plan-task/?isApp=1&uid='+uid">
                <van-badge :content="planTaskCount"
                           max="99">
                  <el-image :src="require('@/assets/img/home/应急预案任务.svg')"></el-image>
                </van-badge>
                <div class="content-row2-text">
                  应急预案
                </div>
              </a>
            </div>
            <div class="content-row2-box"
                 @click="navGoTo('PlanInformation')">
              <el-image :src="require('@/assets/img/home/预案信息.svg')"></el-image>
              <div class="content-row2-text">
                预案信息
              </div>
            </div>
            <div class="content-row2-box">
              <a :href="'http://58.220.201.130:9999/scooper-repo-app/#/repofile?uid='+uid">
                <el-image :src="require('@/assets/img/home/知识库.svg')"></el-image>
                <div class="content-row2-text">
                  知识库
                </div>
              </a>
            </div>
          </div>
        </div>
        <div class="content-row3">
          <div class="content-row3-container"
               @click="navGoTo('OMOrderHead')">
            <van-image class="content-row3-img"
                       :src="require('@/assets/img/home/工单管理.png')"></van-image>

            <div class="content-row3-text">
              <div class="content-row3-text1">工单管理</div>
              <div class="content-row3-text2">昨日工单 {{yesterdayOrder}}</div>
            </div>
          </div>
          <div class="content-row3-container"
               @click="navGoTo('TMTask')">
            <van-image class="content-row3-img"
                       :src="require('@/assets/img/home/任务管理.png')"></van-image>
            <div class="content-row3-text">
              <div class="content-row3-text1">任务管理</div>
              <div class="content-row3-text2">昨日任务 {{yesterdayTask}}</div>
            </div>
          </div>
        </div>
        <div class="content-row4">
          <div class="content-row4-title">运营监测</div>
          <div class="content-row4-box">
            <div class="content-row4-container container1"
                 @click="navGoTo('OTTrafficMonitoring')">
              <div class="content-row4-text">
                <div class="content-row4-text1">客流监测</div>
                <div class="content-row4-text2">检票 <span style="color:#23A867">{{intoNum}}</span></div>
              </div>
              <van-image v-if="checkIsAdmin()"
                         class="content-row4-img"
                         :src="require('@/assets/img/home/客流检测.png')"></van-image>
              <van-image v-else
                         class="content-row4-img"
                         :src="require('@/assets/img/home/客流检测-gray.png')"></van-image>
            </div>
            <div class="content-row4-container container1"
                 @click="navGoTo('OTParkMoney')">
              <div class="content-row4-text">
                <div class="content-row4-text1">园区收入</div>
                <div class="content-row4-text2">今日累计 <span style="color:#23A867">{{toDayYuan}}</span></div>
              </div>
              <van-image v-if="checkIsAdmin()"
                         class="content-row4-img"
                         :src="require('@/assets/img/home/收入检测.png')"></van-image>
              <van-image v-else
                         class="content-row4-img"
                         :src="require('@/assets/img/home/收入检测-gray.png')"></van-image>
            </div>
          </div>
          <div class="content-row4-box">
            <div class="content-row4-container container2"
                 @click="navGoTo('OTequipmentMonitoring')">
              <div class="content-row4-text">
                <div class="content-row4-text1">设备监测</div>
                <div class="content-row4-text2">离线 {{equipmentOffNum}}</div>
              </div>
              <van-image class="content-row4-img"
                         :src="require('@/assets/img/home/设备监测.png')"></van-image>
            </div>
            <div class="content-row4-container container2"
                 @click="navGoTo('OTvideoSurveillance')">
              <div class="content-row4-text">
                <div class="content-row4-text1">视频监控</div>
                <div class="content-row4-text2">在线 {{monitorOnNum}}</div>
              </div>
              <van-image class="content-row4-img"
                         :src="require('@/assets/img/home/视频监控.png')"></van-image>
            </div>
          </div>
        </div>
        <div class="content-row5">
          <div class="content-row5-title">
            报表统计
          </div>
          <div class="content-row5-container">
            <div @click="navGoTo('RSpassengerFlowStatistics')">
              <el-image class="content-row5-img"
                        :src="require('@/assets/img/home/景区客流统计.png')"></el-image>
              <div class="content-row5-text">
                景区客流
              </div>
            </div>
            <div @click="navGoTo('RSticketDataAnalysis')">
              <el-image v-if="checkIsAdmin()"
                        class="content-row5-img"
                        :src="require('@/assets/img/home/票务数据分析.png')"></el-image>
              <el-image v-else
                        class="content-row5-img"
                        :src="require('@/assets/img/home/票务数据分析-gray.png')"></el-image>
              <div class="content-row5-text">
                票务数据
              </div>
            </div>
            <div @click="navGoTo('TouristPortraitAnalysis')">
              <el-image class="content-row5-img"
                        :src="require('@/assets/img/home/游客画像分析.png')"></el-image>
              <div class="content-row5-text">
                游客画像
              </div>
            </div>
            <div @click="navGoTo('AttendanceStatisticalAnalysis')">
              <el-image class="content-row5-img"
                        :src="require('@/assets/img/home/考勤统计分析.png')"></el-image>
              <div class="content-row5-text">
                考勤统计
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  // 闸机出入人数
  gateNum
} from "@/api/chart";
import {
  GetPageContentByConditions as getWork, // 工单查询
} from "@/api/workOrder";
import {
  GetPageContentByConditions as getTask, // 任务查询
} from "@/api/testApi";
import {
  emergencyNum,
  queryPlanTaskCount
} from "@/api/message";
import {
  // 旅游收入分析
  tourismIncome,
  // 大屏售票数
  ticketNumber,
} from "@/api/chart";
import {
  GetEquipmentPagingList,
} from "@/api/mapApi";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data () {
    //这里存放数据
    return {
      uid: null,
      real_name: '',
      orderMessage: 0,
      taskMessage: 0,
      // 昨日工单
      yesterdayOrder: 0,
      // 昨日任务
      yesterdayTask: 0,
      // 应急任务
      eventCount: 0,
      // 预案任务
      planTaskCount: 0,
      // 今日检票数
      intoNum: 0,
      // 今日售票数
      ticketNum: 0,
      // 昨日检票数
      yesterdayInto: 0,
      // 今日销售额
      toDayYuan: 0,
      // 设备离线数量
      equipmentOffNum: 0,
      // 摄像头在线数量
      monitorOnNum: 0
    };
  },
  //监听属性 类似于data概念
  computed: {
  },
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    // 设置底部导航下标
    this.$store.commit("bottomNav", 0)
    // 获取uid
    if (window.android || window.webkit) {
      this.uid = localStorage.getItem("uid")
      this.real_name = JSON.parse(localStorage.getItem("userInfo")).real_name
    } else {
      this.uid = JSON.parse(localStorage.getItem("userInfo")).uid
      this.real_name = JSON.parse(localStorage.getItem("userInfo")).real_name
    }
    // 获取闸机人流 
    this.getGateNum()
    // 大屏售票数
    this.getTicketNumber()
    // 获取工单任务未处理数量
    this.getNewMessage()
    // 获取应急未处理数量
    this.getEmergencyNum()
    // 获取今日销售额
    this.getTourismIncome()
    // 获取设备离线数量
    this.getEquipmentList()
    // 获取摄像头在线数量
    this.getMonitorList()

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.contentBox = document.getElementsByClassName("content-container")[0]
    this.contentBox.scrollTo(0, this.$store.state.homeScroll) // 每次回来将vuex管理的滚动条高度设置上去
  },
  beforeRouteLeave (to, from, next) {
    this.$store.commit("homeScroll", this.contentBox.scrollTop)
    next()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 获取闸机人流
    getGateNum () {
      gateNum().then(res => {
        if (!this.checkIsAdmin()) {
          this.intoNum = '***'
          this.yesterdayInto = '***'
          return
        }
        let data = res.data
        let intoNum = data[data.length - 1].inPeople
        let yesterdayInto = data[data.length - 2].inPeople
        this.intoNum = intoNum
        this.yesterdayInto = yesterdayInto
      })
    },
    // 大屏售票数
    getTicketNumber () {
      ticketNumber().then(res => {
        if (!this.checkIsAdmin()) {
          this.ticketNum = '***'
          return
        }
        let data = res.data
        this.ticketNum = data[data.length - 1].number
      })
    },
    // 获取工单任务数量
    getNewMessage () {
      // 我的工单代办
      let info1 = {
        ChargeUserID: this.uid, // 接收人ID(自己)
        SecunityStatus: 1, // 工单状态 Int 1.待处理，2.已办理,3.已指派
        SecunityContent: "",
        PageSize: 1, // 条数 Int 默认20
        PageIndex: 1, // 页码 Int 默认1
      }
      getWork(info1).then(response => {
        this.orderMessage = response.Data.TotalCount
      })
      // 我的任务
      let info2 = { // 待审核
        TaskStatus: 1,  // 任务状态（1:待审核.2:已审核.3.已驳回,4.已完成)
        ExamineUserId: this.uid, // 审核人id
        TaskTitle: "",
        PageSize: 1, // 条数 Int 默认20
        PageIndex: 1, // 页码 Int 默认1
      }
      getTask(info2).then(response => {
        this.taskMessage = response.Data.TotalCount
      })
      // 工单管理
      let info3 = {
        SecunityStatus: 1, // 工单状态 Int 1.待处理，2.已办理,3.已指派
        SecunityContent: "",
        OperationBeginDate: new Date().format('yyyy-MM-ddT00:00:00'),
        OperationEndDate: new Date().format('yyyy-MM-ddT23:59:59'),
        PageSize: 1, // 条数 Int 默认20
        PageIndex: 1, // 页码 Int 默认1
      }
      getWork(info3).then(response => {
        this.yesterdayOrder = response.Data.TotalCount
      })
      // 任务管理
      let info4 = { // 待审核
        TaskStatus: 1,  // 任务状态（1:待审核.2:已审核.3.已驳回,4.已完成)
        SubmitUserId: this.uid, // 提交人id
        TaskTitle: "",
        SubBeginDate: new Date().format('yyyy-MM-ddT00:00:00'),
        SubEndDate: new Date().format('yyyy-MM-ddT23:59:59'),
        PageSize: 1, // 条数 Int 默认20
        PageIndex: 1, // 页码 Int 默认1
      }
      getTask(info4).then(response => {
        this.yesterdayTask = response.Data.TotalCount
      })
    },
    // 获取应急
    getEmergencyNum () {
      let info = {
        uid: this.uid
      }
      // 我的应急事件
      emergencyNum(info).then(res => {
        this.eventCount = res.data.eventCount
      })
      // 应急预案任务
      queryPlanTaskCount(info).then(res => {
        this.planTaskCount = res.data.planTaskCount
      })
    },
    // 获取今日销售额
    getTourismIncome () {
      tourismIncome('day').then(res => {
        if (!this.checkIsAdmin()) {
          this.toDayYuan = '***'
          return
        }
        let dataList = res.data
        this.toDayYuan = dataList[dataList.length - 1].totalIncome
      })
    },
    // 获取设备离线数量
    getEquipmentList () {
      let info = {
        EquipmentType: "",
        PageSize: 1,
        PageIndex: 1,
        IsOnline: false
      }
      GetEquipmentPagingList(info).then(response => {
        let marketList = response.Data.BaseSearch
        this.equipmentOffNum = marketList.TotalCount
      })
    },
    // 获取摄像头在线数
    getMonitorList () {
      let info = {
        "EquipmentType": "Monitor",
        PageSize: 1,
        PageIndex: 1,
        IsOnline: true
      }
      GetEquipmentPagingList(info).then(response => {
        let monitorList = response.Data.BaseSearch
        this.monitorOnNum = monitorList.TotalCount
      })
    },

    // 判断是否为管理员
    checkIsAdmin () {
      let role = JSON.parse(localStorage.getItem("roleInfoList"))
      if (role) {
        if (role.indexOf('APProleKey') == -1) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },

    navGoTo (routeName) {
      const adminMenu = ['RSticketDataAnalysis', 'OTParkMoney', 'OTTrafficMonitoring']
      // 使用replace跳到个人中心
      if (routeName == "PersonalCenter") {
        this.$router.replace({ name: routeName })
      }

      if (adminMenu.indexOf(routeName) !== -1) {
        if (this.checkIsAdmin()) {
          this.$router.push({ name: routeName })
        } else {
          // this.$toast("没有权限")
          return
        }
      } else {
        this.$router.push({ name: routeName })
      }
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';
