  padding-top: $statusBarHeight;
  height: px2rem(100);
  .top-row1 {
    clear: both;
    height: px2rem(25);
    .top-user {
      float: left;
      display: inline-block;
      margin-left: px2rem(10);
    }
    .top-signIn {
      float: right;
      display: inline-block;
      margin-right: px2rem(10);
    }
    .top-row1-div {
      display: inline-block;
      height: px2rem(25);
      margin: 0 px2rem(3);
      line-height: px2rem(25);
      vertical-align: bottom;
      font-family: PingFangSC-Medium;
      font-weight: 600;
      font-size: 17px;
      color: #333333;
      letter-spacing: 0;
