  }

  .top-row2 {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: px2rem(25);
    .top-tasks {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      align-items: center;
      .top-row2-div {
        font-family: PingFangSC-Regular;
        font-size: 15px;
        color: #333333;
        letter-spacing: 0;
      }
    }
  }
}
