      position: relative;
      width: 95%;
      margin-top: px2rem(40);
      padding-top: px2rem(160);
      background-image: linear-gradient(224deg, #9883ea 0%, #1ed0f9 100%);
      box-shadow: 0 4px 10px 1px rgba(6, 0, 74, 0.2);
      border-radius: 10px;
      border-radius: 10px;
      text-align: left;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #ffffff;
      letter-spacing: 0;
      opacity: 0.8;
      line-height: px2rem(30);
      .onNum {
        position: absolute;
        top: px2rem(20);
        left: px2rem(20);
        display: inline-block;
        .num {
          line-height: px2rem(50);
          font-size: 28px;
        }
      }
      .inNum {
        position: absolute;
        bottom: px2rem(20);
        left: px2rem(20);
        .content-row1-partition {
          display: inline-block;
          width: 1px;
          height: px2rem(15);
          margin: 0 px2rem(15);
          opacity: 0.3;
          background: #ffffff;
        }
        .num {
          margin-left: px2rem(8);
        }
      }
      .content-row1-img {
        position: absolute;
        top: px2rem(-23);
        right: px2rem(0);
        width: px2rem(180);
        height: px2rem(180);
      }
    }
    .content-row2 {
      width: 95%;
      margin-top: px2rem(25);
      .content-row2-box {
        width: px2rem(120);
      }
      .content-row2-title {
        font-family: PingFangSC-Medium;
        font-size: 19px;
        font-weight: 600;
        color: #333333;
        letter-spacing: 0;
        text-align: left;
      }
      .content-row2-emergencys {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        margin: px2rem(20) 0;
        .content-row2-text {
          line-height: px2rem(30);
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #333333;
          letter-spacing: 0;
        }
      }
    }
    .content-row3 {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      width: 95%;
      margin: px2rem(20) 0;

      .content-row3-container {
        width: 48%;
        padding: px2rem(10) 0;
        background: #ffffff;
        box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        border-radius: 10px;
        .content-row3-img {
          float: left;
          width: px2rem(50);
          height: px2rem(50);
