import { detailByUid } from "@/api/userApi";
import {
  getRole,
} from "@/utils/user";

export async function loginAndroid () {
  console.log("安卓登录");
  // 获取android的 uid
  let uid = window.android.loginWithInfo();
  localStorage.setItem("uid", uid)
  // 根据uid获取详情信息
  await detailByUid({ uid }).then(res => {
    localStorage.setItem("userInfo", JSON.stringify(res.data))
    // 获取用户角色
  })
  await getRole()

}

export async function loginIOS () {
  console.log("IOS登录");
  // 获取IOS的 uid
  let uid = window.uid;
  localStorage.removeItem("iosUid");
  localStorage.setItem("uid", uid);

  console.log("IOS登录", uid);

  // 根据uid获取详情信息
  await detailByUid({ uid: uid }).then(res => {
    localStorage.setItem("userInfo", JSON.stringify(res.data))
  })
  // 获取用户角色
  await getRole()
  window.webkit.messageHandlers.reload.postMessage("");
}
