const state = {
  orderNum: localStorage.getItem('orderNum') || '0'
}

const mutations = {
  SET_ORDER_NUM: (state, orderNum) => {
    state.orderNum = orderNum
    localStorage.setItem('orderNum', orderNum)
  }
}

const actions = {
  setOrderNum({ commit }, orderNum) {
    commit('SET_ORDER_NUM', orderNum)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}