import myrequest from '@/utils/axios'
// 引入Im接口的前缀
import { IM_BASE_API, TEST_TOKEN } from "@/utils/ImConfig";

// 分页加载会话信息
export function getConversations (params) {
  return myrequest({
    url: IM_BASE_API + '/scooper-app-msg/message/getConversations',
    method: 'GET',
    params: params
  })
}

// 发起单人会话
export function createSingleConversation (params) {
  return myrequest({
    url: IM_BASE_API + '/scooper-app-msg/message/createSingleConversation',
    method: 'GET',
    params: params
  })
}

