// 上传文件并发送文件消息
export function sendFileMsg (params) {
  return myrequest({
    url: IM_BASE_API + '/scooper-app-msg/message/uploadFileSendMsg?uid=' + JSON.parse(localStorage.getItem("userInfo")).uid,
    method: 'post',
    data: params,
  })
}


