import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";

// 任务查询
export function GetPageContentByConditions (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_task/GetPageContentByConditions',
    method: 'post',
    data: params
  })
}

// 任务更新
export function UpdateItem (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_task/UpdateItem',
    method: 'post',
    data: params
  })
}

// 任务下发记录查询
export function GetTaskuser (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/v_taskuserinfo/GetPageContentByConditions',
    method: 'post',
    data: params
  })
}

// 任务反馈接口
export function UpDateTaskuser (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_taskuser/UpdateItem',
    method: 'post',
    data: params
  })
}

