import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";


// 工单查询
export function GetPageContentByConditions (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_secunity/GetPageContentByConditions',
    method: 'post',
    data: params
  })
}

