import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";


//获取区域列表
export function GetRegionInfos (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/BaseDataService/Region/GetRegionInfos',
    method: 'post',
    data: params
  })
}

// 获取设备列表
export function GetEquipmentPagingList (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/BaseDataService/Equipment/GetEquipmentPagingList',
    method: 'post',
    data: params
  })
}

// 获取LED
export function GetLEDList (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/LEDService/LED/GetLedEquipmentList',
    method: 'post',
    data: params
  })
}

// 获取WIFI
export function GetWIFIList (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/WifiService/Wifi/GetWifiDeviceList',
    method: 'post',
    data: params
  })
}
