import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home'
import Layout from '../views/layout'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/',
    name: 'Layout',
    component: Layout,
    children: [
      {
        path: '/home',
        name: 'Home',
        component: Home
      },
      {
        // 消息
        path: '/message/list',
        name: 'messageList',
        component: () => import('@/views/message')
      },
      {
        // 通讯录
        path: '/addressBook',
        name: 'addressBook',
        component: () => import('@/views/addressBook')
      },
      {
        // 个人中心--个人中心
        path: '/mine/personalCenter',
        name: 'PersonalCenter',
        component: () => import('@/views/mine/personalCenter')
      },
    ]
  },


  {
    path: '/test',
    name: 'Test',
    component: () => import('@/views/test')
  },
  //登录
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/login')
  },
  //登录-人脸识别
  {
    path: '/loginFace',
    name: 'LoginFace',
    component: () => import('@/views/login/loginFace')
  },

  {
    // 我的--签到统计
    path: '/mine/signIn',
    name: 'SignIn',
    component: () => import('@/views/mine/signIn')
  },

  // ------------------------------我的工单头部--------------------------------------
  {
    // 我的工单-头部导航
    path: '/myOrder/orderHead',
    name: 'MOOrderHead',
    component: () => import('@/views/myOrder/orderHead')
  },
  {
    // 我的工单-列表
    path: '/myOrder/orderList',
    name: 'MOOrderList',
    component: () => import('@/views/myOrder/orderList')
  },


  // --------------------------------工单管理头部-------------------------------------
  {
    // 我的工单-头部导航
    path: '/orderManage/orderHead',
    name: 'OMOrderHead',
    component: () => import('@/views/orderManage/orderHead')
  },
  {
    // 我的工单-列表
    path: '/orderManage/orderList',
    name: 'OMOrderList',
    component: () => import('@/views/orderManage/orderList')
  },


  // --------------------------------工单详情模块-------------------------------------
  {
    // 待办工单详情
    path: '/order/workOrderDetails',
    name: 'OrderWorkOrderDetails',
    component: () => import('@/views/order/workOrderDetails')
  },
  {
    //工单处理
    path: '/order/orderProcessing',
    name: 'OrderOrderProcessing',
    component: () => import('@/views/order/orderProcessing')
  },
  {
    // 工单已办详情
    path: '/order/detailsOfProcessedTickets',
    name: 'OrderDetailsOfProcessedTickets',
    component: () => import('@/views/order/detailsOfProcessedTickets')
  },
  //新增工单
  {
    path: '/order/newTicket',
    name: 'OrderNewTicket',
    component: () => import('@/views/order/newTicket')
  },


  // --------------------------------我的任务模块-------------------------------------
  {
    // 我的任务-头部
    path: '/myTask/taskHead',
    name: 'MTTask',
    component: () => import('@/views/myTask/taskHead')
  },
  {
    // 我的任务-列表
    path: '/myTask/taskList',
    name: 'MTTaskList',
    component: () => import('@/views/myTask/taskList')
  },


  // --------------------------------任务管理模块-------------------------------------
  {
    // 任务管理-头部
    path: '/taskManage/taskHead',
    name: 'TMTask',
    component: () => import('@/views/taskManage/taskHead')
  },
  {
    // 任务管理-列表
    path: '/taskManage/taskList',
    name: 'TMTaskList',
    component: () => import('@/views/taskManage/taskList')
  },


  // --------------------------------任务详情模块-------------------------------------
  {
    //任务审核
    path: '/task/taskReview',
    name: 'TaskTaskReview',
    component: () => import('@/views/task/taskReview')
  },
  {
    // 已核任务详情
    path: '/task/taskDetails',
    name: 'TaskTaskDetails',
    component: () => import('@/views/task/taskDetails')
  },
  {
    //业务处理--已阅
    path: '/task/haveRead',
    name: 'TaskHaveRead',
    component: () => import('@/views/task/haveRead')
  },
  {
    //业务处理--未阅
    path: '/task/notRead',
    name: 'TaskNotRead',
    component: () => import('@/views/task/notRead')
  },


  //运营检测---------------------------------------------------------
  {
    //客流监测
    path: '/operationalTesting/trafficMonitoring',
    name: 'OTTrafficMonitoring',
    component: () => import('@/views//operationalTesting/trafficMonitoring')
  },
  {
    //园区收入实时分析
    path: '/operationalTesting/parkMoney',
    name: 'OTParkMoney',
    component: () => import('@/views//operationalTesting/parkMoney')
  },
  {
    // 运营监测--设备监控
    path: '/operationalTesting/equipmentMonitoring',
    name: 'OTequipmentMonitoring',
    component: () => import('@/views/operationalTesting/equipmentMonitoring')
  },
  {
    // 运营监测--视频监控
    path: '/operationalTesting/videoSurveillance',
    name: 'OTvideoSurveillance',
    component: () => import('@/views/operationalTesting/videoSurveillance')
  },
  {
    // 运营监测--视频播放
    path: '/operationalTesting/playVideo',
    name: 'OTplayVideo',
    component: () => import('@/views/operationalTesting/videoSurveillance/playVideo')
  },
  {
    //人员监控
    path: '/operationalTesting/personnelMonitoring',
    name: 'OTPersonnelMonitoring',
    component: () => import('@/views/operationalTesting/personnelMonitoring')
  },
  {
    //视频回放
    path: '/operationalTesting/videoPlayback',
    name: 'OTVideoPlayback',
    component: () => import('@/views/operationalTesting/videoPlayback')
  },
  //报表统计--游客画像分析
  {
    path: '/reportStatistics/touristPortraitAnalysis',
    name: 'TouristPortraitAnalysis',
    component: () => import('@/views/reportStatistics/touristPortraitAnalysis')
  },
  //报表统计--考勤统计分析
  {
    path: '/reportStatistics/attendanceStatisticalAnalysis',
    name: 'AttendanceStatisticalAnalysis',
    component: () => import('@/views/reportStatistics/attendanceStatisticalAnalysis')
  },

  //报表统计
  {
    // 报表统计--景区客流统计
    path: '/reportStatistics/passengerFlowStatistics',
    name: 'RSpassengerFlowStatistics',
    component: () => import('@/views/reportStatistics/passengerFlowStatistics')
  },
  {
    // 报表统计--票务数据分析
    path: '/reportStatistics/ticketDataAnalysis',
    name: 'RSticketDataAnalysis',
    component: () => import('@/views/reportStatistics/ticketDataAnalysis')
  },
  {
    // 运营监控--服务设施监控
    path: '/operationalTesting/serviceFacilityMonitoring',
    name: 'serviceFacilityMonitoring',
    component: () => import('@/views/operationalTesting/serviceFacilityMonitoring')
  },
  {
    // 运营监控--服务设施监控
    path: '/operationalTesting/trafficStateMonitoring',
    name: 'TrafficStateMonitoring',
    component: () => import('@/views/operationalTesting/trafficStateMonitoring')
  },
  {
    // 个人中心--我的基本信息
    path: '/mine/myBasicInfo',
    name: 'MyBasicInfo',
    component: () => import('@/views/mine/myBasicInfo')
  },
  {
    // 业务处理--预案信息
    path: '/businessProcess/planInformation',
    name: 'PlanInformation',
    component: () => import('@/views/businessProcess/planInformation')
  },
  {
    // 业务处理--预案信息--预案信息详情
    path: '/businessProcess/detailsOfPlanInformation',
    name: 'DetailsOfPlanInformation',
    component: () => import('@/views/businessProcess/detailsOfPlanInformation')
  },
  {
    // 聊天对话页面
    path: '/message/chat',
    name: 'MessageChat',
    component: () => import('@/views/message/chat')
  },
  {
    // 通讯录多选页面
    path: '/addressBook/checkAddressBook',
    name: 'checkAddressBook',
    component: () => import('@/views/addressBook/checkAddressBook')
  }
]

const router = new VueRouter({
  mode: 'hash',
  routes
})

export default router

