import axios from 'axios'
import {
  getAccessTokenByPort, // 获取新access_token
} from "@/utils/user";

// create an axios instance
const service = axios.create({
  baseURL: '', // url = base url + request url
  // baseURL: 'http://39.105.109.252:9000',
  // withCredentials: true, // send cookies when cross-domain requests
  timeout: 50000, // request timeout

  // 下面两个属性，用来设置，请求失败或者超时，自动重新请求的次数和间隙时间
  retry: 2, // 请求次数
  retryInterval: 800 // 求期间隙

})

// request interceptor
service.interceptors.request.use(
  config => {
    // do something before request is sent

    // if (store.getters.token) {
    //   // let each request carry token
    //   // ['X-Token'] is a custom headers key
    //   // please modify it according to the actual situation
    //   config.headers['X-Token'] = getToken()
    // }
    return config
  },
  error => {
    // do something with request error
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  /**
   * If you want to get http information such as headers or status
   * Please return  response => response
  */

  /**
   * Determine the request status by custom code
   * Here is just an example
   * You can also judge the status by HTTP Status Code
   */
  response => {
    const res = response.data
    // 获取axios实体，用于重新请求
    const config = response.config
    // if the custom code is not 20000, it is judged as an error.
    if (res.RspCode && res.RspCode !== '1') {
      return Promise.reject(new Error(res.RspMsg || 'Error'))
    } else if (res.code && res.code == 10001) {
      console.error("统一认证平台access_token过期");
      /***
       * 获取新的acess_token
       */
      getAccessTokenByPort()
      // localStorage.setItem("access_token", "RlhaV3gxYks1VVFXMjB3QmdmLzVaT24yZ3JoZWR1UjdIOXEyUVhrNVdXSE52SzA2VVFSNkkzOTIwRVB3SjhXMjIvN3k5dXQ0UytYWTBpODIwMm5LZER1NE92ZXBXVGx4eXlxWTVvZW5Ld0E9")
      let url = config.url.split("?")[0]
      config.url = url + "?access_token=" + localStorage.getItem("access_token")
      //如果配置不存在或重试属性未设置，抛出promise错误
      if (!config || !config.retry) {
        return Promise.reject(res)
      }
      //设置一个变量记录重新请求的次数
      config.retryCount = config.retryCount || 0;
      // 检查重新请求的次数是否超过我们设定的请求次数
      if (config.retryCount >= config.retry) {
        return res;
      }
      //重新请求的次数自增
      config.retryCount += 1;

      // 创建新的Promise来处理重新请求的间隙
      var back = new Promise(function (resolve) {
        console.log("接口" + config.url + "请求超时，重新请求")
        setTimeout(function () {
          resolve();
        }, config.retryInterval || 1);
      });
      //返回axios的实体，重试请求
      return back.then(function () {
        return service(config)
      });

    } else {
      return res
    }
  },
  error => {
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

export default service