Date.prototype.format = function (fmt) {
  var o = {
    "M+": this.getMonth() + 1,
    "d+": this.getDate(),
    "h+": this.getHours(),
    "m+": this.getMinutes(),
    "s+": this.getSeconds(),
    "q+": Math.floor((this.getMonth() + 3) / 3),
    "S": this.getMilliseconds()
  }; if (/(y+)/.test(fmt)) { fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length)); } for (var k in o) { if (new RegExp("(" + k + ")").test(fmt)) { fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length))); } }
  return fmt;
}

window.randerMessage = function (mes) {
  console.log("被原生调用", mes);
  this.$bus.emit('randerMessage', { message: '你好' });
}


if (window.webkit) {
  var orignalSetItem = localStorage.setItem;
  localStorage.setItem = function (key, newValue) {
    var setItemEvent = new Event("setItemEvent");
    setItemEvent.newValue = newValue;
    window.dispatchEvent(setItemEvent);
    orignalSetItem.apply(this, arguments);
  }
  window.addEventListener("setItemEvent", function () {
    let flag = false;
    if (localStorage.getItem("iosUid")) {
      window.uid = localStorage.getItem("iosUid")
      localStorage.removeItem("iosUid");
      flag = true
    }
    if (flag === true) {
      loginIOS();
    }
  });
}

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')


